import React, { useState, useEffect, useRef } from 'react';
import LoginBox from '../boxes/LoginBox';
import SignUpBox from '../boxes/SignUpBox';
import Navbar from '../components/Navbar';
import PasswordBox from '../boxes/PasswordBox';
import EmailBox from '../boxes/EmailBox';
import MobileNavbar from '../boxes/MobileNavbar';
import { useNavigate } from 'react-router-dom';
import { auth,  } from '../firebase'; 
import { onAuthStateChanged } from "firebase/auth";

function PricingSection() {
  const [isFeatured, setIsFeatured] = useState(2);
  const [showLoginBox, setShowLoginBox] = useState(false);
  const [showSignUpBox, setShowSignUpBox] = useState(false);
  const [showPasswordBox, setShowPasswordBox] = useState(false);
  const [showEmailBox, setShowEmailBox] = useState(false);
  const [isPopupOpen, setIsPopupOpen] = useState(false);
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  

  const handleBoxHover = (boxNumber) => {
    setIsFeatured(boxNumber);
  };

  const handleBoxLeave = () => {
    setIsFeatured(0);
  };

  
  const navigate = useNavigate();
  const popupRef = useRef(null);

  const closeAllPopups = () => {
      setShowLoginBox(false);
      setShowSignUpBox(false);
      setShowPasswordBox(false);
      setShowEmailBox(false);
      setIsPopupOpen(false); 
  };

  const showSignUp = () => {
      closeAllPopups();
      setShowSignUpBox(true);
  };

  const showPassword = () => {
      closeAllPopups();
      setShowPasswordBox(true);
  };

  const showEmail = () => {
      closeAllPopups();
      setShowEmailBox(true);
  };

  const handleLoginButtonClick = () => {
      closeAllPopups();
      setShowLoginBox(true);
      setIsLoggedIn(true);
  };
  
  useEffect(() => {
      const handleKeyPress = (event) => {
      if (event.key === 'Escape') {
          closeAllPopups();
      }
      };

      const handleClickOutside = (event) => {
      if (
          popupRef.current &&
          !popupRef.current.contains(event.target) &&
          event.target.getAttribute('id') !== 'loginButtonNav'
      ) {
          closeAllPopups();
      }
      };

      window.addEventListener('keydown', handleKeyPress);
      document.addEventListener('mousedown', handleClickOutside);

      return () => {
      window.removeEventListener('keydown', handleKeyPress);
      document.removeEventListener('mousedown', handleClickOutside);
      };
  }, []);

  // Check if the screen width is less than or equal to 768px (mobile devices)
  const isMobile = window.innerWidth <= 768;

  useEffect(() => {
      // Disable scrolling on mobile devices when a popup is open
      if (isMobile) {
      document.body.style.overflow = showLoginBox || showSignUpBox || showPasswordBox || showEmailBox
          ? 'hidden'
          : 'auto';
      }

      return () => {
      // Enable scrolling when popups are closed
      if (isMobile) {
          document.body.style.overflow = 'auto';
      }
      };
  }, [showLoginBox, showSignUpBox, showPasswordBox, showEmailBox, isMobile]);

  const checkLoginStatus = () => {
    onAuthStateChanged(auth, (user) => {
      if (user) {
        // User is logged in
        setIsLoggedIn(true);
      } else {
        // User is logged out
        setIsLoggedIn(false);
      }
    });
  };
  
  useEffect(() => {
    // Check login status when component mounts
    checkLoginStatus();
  }, []);


  





  return (

    <div>

    {showLoginBox || showSignUpBox || showPasswordBox || showEmailBox ? (
      <div>
        <div className="blurred-background"></div>
   
      </div>
    ) : null}
      <div ref={popupRef}>
        {showLoginBox && (
          <LoginBox
            showSignUpBox={showSignUp}
            showPasswordBox={showPassword}
            showEmailBox={showEmail}
            onLoginSuccess={closeAllPopups}
          />
        )}
        {showSignUpBox && (
          <SignUpBox
            onGoBack={handleLoginButtonClick}
            onSignUpSuccess={() => {}}
            showPasswordBox={showPassword}
            closeAllPopups={closeAllPopups}
          />
        )}
        {showEmailBox && (
          <EmailBox
            onGoBack={handleLoginButtonClick}
            onSignUpSuccess={() => {}}
            showEmailBox={showEmailBox}
          />
        )}
        {showPasswordBox && <PasswordBox onGoBack={handleLoginButtonClick} />}
      </div>




    <section id="pricing" className="pricing general-padding" >
      
      <div className="container-lg" data-aos="fade-up">

        <div className="section-title">
          <h2>Pricing</h2>
          <p>At Tio, our commitment is to empower experts. We believe in transparency, so here's the deal: Experts on Tio pay only a simple monthly fee. There are no hidden costs, and what you charge for your expertise is entirely yours to keep. Tio doesn't deduct any commissions. We only request that you offer users the best of your knowledge and skills.</p>
        </div>
    

        <div className="row">

        <div
          className={`col-lg-4 ${isFeatured === 1 ? 'featured' : ''}`}
          onMouseEnter={() => handleBoxHover(1)}
          onMouseLeave={handleBoxLeave}
          data-aos="fade-up"
          data-aos-delay="100"
        >            
            <div className="box">
            <p>The Free Plan offers access to our Tio's services. Ask unlimited questions and access to all experts</p>

              <h3>Free Plan</h3>
              <h4><sup>£</sup>0<span>per month</span></h4>
              <ul>
                <li><i className="bx bx-check"></i> All users</li>
                <li><i className="bx bx-check"></i> Unlimited Questions</li>
                <li><i className="bx bx-check"></i> Acces to all Experts</li>
                <li className="na"><i className="bx bx-x"></i> <span>Pharetra massa massa ultricies</span></li>
                <li className="na"><i className="bx bx-x"></i> <span>Massa ultricies mi quis hendrerit</span></li>
              </ul>
              {isLoggedIn ? (
  <div className="buy-btn current-plan">
 Current Plan
</div>
) : (
  <a href="#" className="buy-btn" onClick={handleLoginButtonClick}>
    Get Started
  </a>
)}

            </div>
          </div>
          <div
          className={`col-lg-4 mt-4 mt-lg-0 ${isFeatured === 2 ? 'featured' : ''}`}
          onMouseEnter={() => handleBoxHover(2)}
          onMouseLeave={handleBoxLeave}
          data-aos="fade-up"
          data-aos-delay="200"
        >
            <div className="box">
            <p>The Expert Plan is designed for Experts. It's requried for all users who want to become an Expert.</p>
              <h3>Expert Plan</h3>
              <h4><sup>£</sup>4.99<span>per month</span></h4>
              <ul>
                <li><i className="bx bx-check"></i> Acces to resources</li>
                <li><i className="bx bx-check"></i> Pop-up notifications</li>
                <li><i className="bx bx-check"></i> Direct messaging</li>
                <li><i className="bx bx-check"></i> Unlimited Support</li>
                <li><i className="bx bx-check"></i> User Feedback Reports</li>
              </ul>
              <a href="#" className="buy-btn">Get Started</a>
            </div>
          </div>

          <div
          className={`col-lg-4 mt-4 mt-lg-0 ${isFeatured === 3 ? 'featured' : ''}`}
          onMouseEnter={() => handleBoxHover(3)}
          onMouseLeave={handleBoxLeave}
          data-aos="fade-up"
          data-aos-delay="300"
        >            <div className="box">
            <p>Our Expert Enterprise Plan is tailored for businesses and power users who need advanced capabilities.</p>

              <h3>Expert Enterprise Plan</h3>
              <h4><sup>£</sup>9.99<span>per month</span></h4>
              <ul>
                <li><i className="bx bx-check"></i> Multiple Experts</li>
                <li><i className="bx bx-check"></i> Acces to all user questions</li>
                <li><i className="bx bx-check"></i> Priority Support</li>
                <li><i className="bx bx-check"></i> API Integration</li>
                <li><i className="bx bx-check"></i> Premium Plugins</li>
              </ul>
              <a href="#" className="buy-btn">Get Started</a>
            </div>
          </div>
          </div>
  
          </div>

    </section>
    </div>
  );
}

export default PricingSection;
